n.user-row:first-child span:first-child,\n.nav-header:first-child span:first-child,\n.tag-header:first-child span:first-child {\n  width: 100%;\n  height: 2px;\n  background-color: rgb(87, 111, 114);\n}\n\n.user-row:first-child span:nth-child(3),\n.nav-header:first-child span:nth-child(3),\n.tag-header:first-child span:nth-child(3) {\n  width: 100%;\n  height: 2px;\n  background-color: rgb(87, 111, 114);\n}\n\n.user-details .user-row:nth-child(1),\n.nav-buttons .nav-header:nth-child(1),\n.tags > .tag-header:nth-child(1) {\n  font-size: 1.2rem;\n  font-weight: 600;\n  font-family: \"Montserrat\", \"Poppins\", var(--default-fonts);\n  text-transform: capitalize;\n}\n\n.user-row {\n  display: flex;\n  align-items: center;\n  gap: 16px;\n}\n\n.user-row .user-picture {\n  border-radius: 50%;\n}\n\n.user-row .notifications {\n  margin-left: auto;\n}\n\n.nav-buttons {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-evenly;\n  gap: 16px;\n}\n\n.nav-buttons .nav-row,\n.nav-buttons .nav-header {\n  display: flex;\n  align-items: center;\n  gap: 16px;\n}\n\n.nav-row > p {\n  cursor: pointer;\n  transition: transform 0.2s;\n}\n\n.nav-row > p:hover {\n  transform: translateY(-1px);\n}\n\n.tags {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-evenly;\n  gap: 16px;\n}\n\n.tags .tag-row,\n.tags .tag-header {\n  display: flex;\n  align-items: center;\n  gap: 16px;\n}\n\n.tag-row p {\n  cursor: pointer;\n  margin-right: auto;\n  transition: transform 0.2s;\n}\n\n.tag-row p:hover {\n  transform: translateY(-1px);\n}\n\n.tags > button {\n  border: 1px solid rgb(87, 111, 114);\n  border-radius: 5px;\n  outline: none;\n  /* background-color: rgb(87, 111, 114, 0); */\n  background: none;\n  cursor: pointer;\n  transition: 0.5s;\n  font-size: 1.2rem;\n  color: rgba(87, 111, 114, 0.5);\n}\n\n.tags > button:hover,\n.tags > button:active {\n  color: rgba(87, 111, 114, 1);\n}\n\n#content {\n  grid-column: 2 / 3;\n  padding: 48px;\n  background-color: rgb(240, 235, 227);\n}\n\n.header > .header-details > h2 {\n  font-size: 1.6rem;\n  font-family: \"Montserrat\", \"Poppins\", var(--default-fonts);\n}\n\n.header {\n  display: flex;\n}\n\n.header-details {\n  display: flex;\n  align-items: center;\n  gap: 16px;\n}\n\n.header-details > .material-symbols-outlined {\n  font-size: 2rem;\n}\n\n.header-buttons {\n  display: flex;\n  align-items: center;\n  margin-left: auto;\n}\n\n.cards {\n  padding: 48px;\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 32px;\n}\n\n.card {\n  height: 5vh;\n  background-color: rgb(228, 220, 207);\n  border-radius: 5px;\n  border-left: 10px solid rgb(87, 111, 114);\n  display: flex;\n  align-items: center;\n  gap: 16px;\n  padding: 16px;\n  color: rgb(87, 111, 114);\n}\n\n.empty-card {\n  height: 5vh;\n  background-color: rgb(228, 220, 207);\n  border-radius: 5px;\n  border-left: 10px solid rgb(87, 111, 114);\n  display: flex;\n  align-items: center;\n  gap: 16px;\n  padding: 16px;\n}\n\n.card-info {\n  display: grid;\n  grid-auto-flow: column;\n  align-items: center;\n}\n\n.card-title {\n  text-transform: lowercase;\n  transform: translateY(-1px);\n  font-size: 1.3rem;\n  font-weight: 500;\n  font-family: \"Montserrat\", \"Poppins\", var(--default-fonts);\n  align-self: center;\n}\n\n.card-tags {\n  text-transform: lowercase;\n  font-weight: 300;\n}\n\n.card .card-buttons {\n  display: flex;\n  align-items: center;\n  gap: 16px;\n  margin-left: auto;\n}\n\n.empty-card {\n  align-items: center;\n  justify-content: center;\n  cursor: pointer;\n}\n\n.empty-card span {\n  color: rgb(87, 111, 114);\n  font-size: 2.5rem;\n  opacity: 0.5;\n  transition: all 0.5s;\n}\n\n.empty-card:hover > span {\n  transform: translateY(0px) rotate(0deg);\n  opacity: 1;\n}\n\ndialog[open] {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  height: 100vh;\n  max-height: 100vh;\n  width: 100%;\n  max-width: 100vw;\n  background: none;\n  overflow: hidden;\n}\n\ndialog[open]::after {\n  content: \"\";\n  height: 100vh;\n  width: 100%;\n  backdrop-filter: blur(10px);\n  position: absolute;\n  z-index: 0;\n}\n\n.wrapper {\n  z-index: 1;\n  padding: 64px;\n  background-color: rgba(228, 220, 207, 1);\n  border-radius: 5px;\n  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);\n  position: relative;\n}\n\n.dialog-form {\n  display: flex;\n  flex-direction: column;\n  gap: 24px;\n}\n\n.dialog-form .form-close {\n  position: absolute;\n  right: 0;\n  top: 0;\n  margin: 12px;\n  padding: 0 8px;\n  outline: none;\n  border: 2px solid rgb(87, 111, 114);\n  font-size: 1.5rem;\n  background: none;\n  align-self: end;\n  border-radius: 5px;\n  transition: 0.2s;\n}\n\n.form-close::after {\n  content: \"\\00d7\";\n}\n\n.form-close:hover {\n  background-color: rgb(87, 111, 114);\n  color: rgb(240, 235, 227);\n}\n\n.dialog-form input:not([type=\"checkbox\"]),\n.dialog-form select,\n.dialog-form textarea {\n  background: rgb(240, 235, 227);\n  outline: none;\n  border: none;\n  border-radius: 5px;\n  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);\n  padding: 2px 8px;\n  font-size: 0.9rem;\n  transition: color 0.2s;\n}\n\n.dialog-form input:not([type=\"checkbox\"]),\n.dialog-form textarea,\n.dialog-form select {\n  color: rgba(0, 0, 0, 0.5);\n}\n\n.dialog-form input:first-of-type::placeholder {\n  color: rgb(255, 101, 119);\n  text-align: end;\n  transition: 0.5s;\n}\n\n.dialog-form input:not([type=\"checkbox\"]):hover,\n.dialog-form input:not([type=\"checkbox\"]):focus,\n.dialog-form textarea:hover,\n.dialog-form textarea:focus,\n.dialog-form select:hover,\n.dialog-form select:focus {\n  color: rgb(0, 0, 0);\n}\n\n.dialog-form textarea {\n  width: 300px;\n  height: 150px;\n  resize: none;\n}\n\n.dialog-form label:not(.checkbox) {\n  display: flex;\n  align-items: center;\n  font-size: 1.2rem;\n  font-family: \"Montserrat\", \"Poppins\", var(--default-fonts);\n  gap: 12px;\n}\n\n.dialog-form label:not(.checkbox) span:first-child {\n  width: 100%;\n  height: 2px;\n  background-color: rgb(87, 111, 114);\n}\n\n.dialog-form label:not(.checkbox) span:last-child {\n  width: 100%;\n  height: 2px;\n  background-color: rgb(87, 111, 114);\n}\n\nlabel.checkbox {\n  position: relative;\n  margin: 5px 0;\n  cursor: pointer;\n}\n\nlabel.checkbox input {\n  appearance: none;\n  display: none;\n}\n\nlabel.checkbox span {\n  position: relative;\n  display: block;\n  width: 40px;\n  height: 25px;\n  background: rgb(228, 220, 207);\n  border-radius: 40px;\n  box-shadow: inset 0 2px 15px rgba(0, 0, 0, 0.2);\n}\n\nlabel.checkbox .indicator {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 25px;\n  height: 25px;\n  background: linear-gradient(to bottom, rgb(125, 157, 156), rgb(87, 111, 114));\n  border-radius: 50%;\n  box-shadow:\n    0 2px 5px rgba(0, 0, 0, 0.5),\n    inset 0 1px 1px rgba(255, 255, 255, 0.2);\n  transition: 0.5s;\n}\n\nlabel.checkbox input:checked ~ .indicator {\n  left: 15px;\n}\n\nlabel.checkbox .indicator::before {\n  content: \"\";\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  width: 3px;\n  height: 3px;\n  background: rgb(255, 0, 0);\n  border-radius: 50%;\n  transition: 0.5s;\n}\n\nlabel.checkbox input:checked ~ .indicator::before {\n  background: rgb(0, 255, 0);\n}\n\n.dialog-form .form-button {\n  border: 2px solid rgb(87, 111, 114);\n  border-radius: 5px;\n  outline: none;\n  background-color: rgb(87, 111, 114, 0);\n  cursor: pointer;\n  transition: 0.2s;\n}\n.dialog-form > .form-button:hover,\n.tags > .form-button:active {\n  background-color: rgb(87, 111, 114);\n  color: rgb(240, 235, 227);\n}\n\n.dialog-form input[type=\"color\"] {\n  align-self: center;\n}\n"],"sourceRoot":""}]);
